<template>
    <yd-layout>
        <div class="headerTime">
            <yd-flexbox>
                <div class="year">年份：</div>
                <div class="yearValue" v-text="yearObj.alyear"></div>
                <yd-flexbox-item>
                    <div class="item">可休年假开始日期：</div>
                </yd-flexbox-item>
                <div class="date" v-text="yearObj.startdate"></div>
            </yd-flexbox>
        </div>
        <div class="part_one">
            <div class="fluidWidth">
                <yd-flexbox>
                    <div class="continue_left">延用年假</div>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="continue_num" v-text="yearObj.lastyearall"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">总天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="continue_has" v-text="yearObj.lastyearthen"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">已休天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="continue_num" v-text="usedLastDays"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">剩余天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="part_one">
            <div class="fluidWidth">
                <yd-flexbox>
                    <div class="continue_left">核定年假</div>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="blue_word" v-text="yearObj.yearall"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">总天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="continue_has" v-text="yearObj.yearthen"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">已休天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="blue_word" v-text="approLastDays"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">剩余天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="part_one">
            <div class="fluidWidth">
                <yd-flexbox>
                    <div class="continue_left">全年总计</div>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="blue_word" v-text="totalDay"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">总天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="continue_has" v-text="totalHasDays"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">已休天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <div class="blue_word" v-text="totalLastDays"></div>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <div class="continue_word">剩余天数</div>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="fluidWidth part_two">
            <yd-flexbox>
                <div class="continue_left">当年已休明细</div>
            </yd-flexbox>
            <yd-flexbox>
                <div class="timeRange">已使用年假<span class="totalNum">{{totalHasDays}}</span>天（含过期延用年假）</div>
            </yd-flexbox>
        </div>
        <div class="part_three">
            <div class="fluidWidth">
                <div class="prompt">
                    提示
                </div>
                <yd-flexbox>
                    <div class="text_one">
                        <span class="line_one">上年度可休年假使用截止日期为：</span><span class="line_two">{{yearObj.alyear}}年3月31日</span><span class="line_three">过期后作废。</span>
                    </div>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="text_one">
                        <span class="line_two">4月1日</span><span class="line_one">起，</span><span class="line_three">上年度可延用年假剩余天数将全部清零。</span><span class="line_one">若您对休假天数有疑问，请联系本部门综合员。</span>
                    </div>
                </yd-flexbox>
            </div>
        </div>
        <yd-tabbar slot="tabbar" v-if="flag">
            <div class="assistBottom">
                <router-link to="/history/true/undefined">
                    <div class="examineLogo btnOne">
                        <yd-flexbox>
                            <div class="examineLogo_txt">查看休假申请</div>
                        </yd-flexbox>
                    </div>
                </router-link>
                <div class="examineLogo btnTwo" @click="toSelect">
                    <yd-flexbox>
                        <div class="examineLogo_txt">发起休假申请</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'myHoliday',
    data() {
        return {
            yearObj: [],
            totalDay: '', // 全年总计总天数
            totalHasDays: '', // 全年总计已休天数
            totalLastDays: '', // 全年总计剩余天数
            usedLastDays: '', // 沿用年假剩余
            approLastDays: '', // 核定年假剩余
            flag: true
        }
    },
    mounted() {
        document.setTitle('我的年假');
        this.annualLeave();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        annualLeave() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/AnnLeave/annual/getAnnualLeave', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData.length == 0) {
                    vm.flag = false;
                    vm.yearObj = {
                        lastyearall: 0,
                        lastyearthen: 0,
                        pkid: 0,
                        yearall: 0,
                        yearthen: 0
                    };
                    let thisYear = vm.formatDate(new Date(), 'yyyy');
                    vm.yearObj.alyear = thisYear;
                    vm.yearObj.startdate = thisYear + '-01-01';
                    vm.totalDay = 0;
                    vm.totalHasDays = 0;
                    vm.totalLastDays = 0;
                    vm.usedLastDays = 0;
                    vm.approLastDays = 0;
                    vm.$dialog.toast({
                        mes: '你没有年假权限',
                        timeout: 2000
                    });
                } else {
                    var data = res.jsonData[0];
                    vm.yearObj = data;
                    vm.totalDay = vm.yearObj.lastyearall + vm.yearObj.yearall;
                    vm.totalHasDays = vm.yearObj.lastyearthen + vm.yearObj.yearthen;
                    vm.totalLastDays = vm.yearObj.lastyearall - vm.yearObj.lastyearthen + vm.yearObj.yearall - vm.yearObj.yearthen;
                    vm.usedLastDays = vm.yearObj.lastyearall - vm.yearObj.lastyearthen;
                    vm.approLastDays = vm.yearObj.yearall - vm.yearObj.yearthen;
                }
            });
        },
        toSelect() {
            this.$router.push('/selected/0');
        }
    }
}
</script>
<style scoped>
.part_one {
    height: 1.55rem;
    width: 100%;
    background-color: #fff;
    margin-bottom: 0.04rem;
}

.continue_left {
    text-align: center;
    font-size: 0.3rem;
    line-height: 0.8rem;
    color: #333;
}

.prompting {
    text-align: center;
    font-size: 0.3rem;
    line-height: 0.5rem;
    color: #333;
}

.continue_num {
    font-size: 0.38rem;
    color: #666;
    margin-bottom: 0.13rem;
    text-align: center;
    margin: 0 auto;
    width: 1.4rem;
    font-weight: bold;
}

.continue_word {
    text-align: center;
    width: 1.4rem;
    font-size: 0.26rem;
    margin: 0 auto;
    margin-top: 0.13rem;
}

.continue_has {
    font-size: 0.34rem;
    text-align: center;
    margin: 0 auto;
    width: 1.4rem;
    color: #b0b0b0;
    font-weight: bold;
}

.blue_word {
    font-size: 0.34rem;
    font-weight: bold;
    margin-bottom: 0.13rem;
    text-align: center;
    margin: 0 auto;
    color: #35a4ff;
    width: 1.4rem;
}

.part_two {
    background-color: #fff;
    margin-top: 0.14rem;
    padding-top: 0rem;
    padding-bottom: .27rem;
}

.timeRange {
    font-size: 0.28rem;
}

.totalDay {
    float: right;
    font-size: 0.28rem;
}

.totalNum {
    color: #35a4ff;
    font-weight: bold;
    padding: 0 0.1rem;
}

.part_three {
    height: 4rem;
    background-color: #fff;
    margin-top: 0.14rem;
}

.line_one {
    font-size: 0.28rem;
    color: #666;
    /*font-weight: bold;*/
}

.line_two {
    font-size: 0.28rem;
    color: #35a4ff;
    font-weight: bold;
    padding-right: 0.1rem;
}

.line_three {
    font-size: 0.28rem;
    color: #333;
    font-weight: bold;
}

.text_one {
    line-height: 0.5rem;
}

.part_four {
    /*height: 2rem;*/
    padding: 0 0.2rem;
    position: fixed;
    width: 100%;
    bottom: 0rem;
    left: 0;
    background-color: #f1f1f1;
}

.yd-btn-block {
    margin-bottom: 0.2rem;
}

.yd-tabbar {
    padding: 0;
}

.examineLogo {
    background-color: #61b3f6;
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo_txt {
    width: 100%;
    height: 0.99rem;
    line-height: .99rem;
    color: #fff;
    font-size: .32rem;
    font-weight: normal;
}

.btnTwo {
    background-color: #ff536f;
}

.prompt {
    font-size: .3rem;
    color: #333;
    padding-bottom: 0.25rem;
}

.select_leader {
    height: 100%;
}
</style>


